
import {createRouter, createWebHistory} from 'vue-router'
import LoginForm from '../views/LoginForm.vue';
import RegisterForm from '../views/RegisterForm.vue';
import LabelRecommend from '../views/LabelRecommend.vue';
import RealTimeRecommend from '../views/RealTimeRecommend.vue';
import BookInfo from '../views/BookInfo.vue';
import SearchResults from '../views/SearchResults.vue'
import TagSelection from '../views/Tag.vue' 
 
const routerHistory = createWebHistory()

const router = createRouter({
  history: routerHistory,
   /*
    *   在这里和vue2一样写路由配置
   */
   routes : [
    { path: '/', component: LoginForm },
    { path: '/login', component: LoginForm },
    { path: '/register', component: RegisterForm },
    {path: '/label-recommend/:userid', name: 'labelrecommend',component: LabelRecommend},
    {path: '/realtime-recommend/:userid', name: 'realtimerecommend',component: RealTimeRecommend},
    {path: '/book/:bookid', name: 'book',component: BookInfo},
    {path: '/search/:query', name: 'search',component: SearchResults},
    { path: '/tag/:userid',name: 'tag', component: TagSelection }
  ]
}
)


export default router;