<template>
  <AfterLoginHeader></AfterLoginHeader>
    <div>
      <h2>您想要找的作品如下……</h2>
      <ul>
        <li v-for="book in searchedBooks" :key="book.bookId" class="book-item" @click="goToBookDetails(book.bookId)">
          <div class="book-image">
            <img :src="book.imlUrl" :alt="book.name" />
          </div>
          <div class="book-details">
            <h3>{{ book.name }}</h3>
            <p v-if="book.authors.length">作者: {{ book.authors }}</p>
            <p v-if="book.translators.length">译者: {{ book.translators }}</p>
            <p>评分: {{ book.rating }}</p>
            <p>出版社: {{ book.publisher }}</p>
          </div>
        </li>
      </ul>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import store from '@/store';
  import AfterLoginHeader from '@/components/Header.vue';

  export default {
    name:'SearchResults',
    components:{
      AfterLoginHeader
    },
    data() {
      return {
        searchedBooks: []
      };
    },
    computed: {
      userId() {
        return store.state.userid;
      
      }
    },
    created() {
      // 发送get请求
      axios
        .get('/search', {
          params: {
            query: this.$route.params.query
          }
        })
        .then(response => {
          // 处理搜索结果响应
            console.log(response.data)
            const searchBooks = response.data;
            // this.searchedBooks=JSON.parse(JSON.stringify(searchBooks));
            var str=JSON.stringify(searchBooks)
              // console.log(str)
              this.searchedBooks = JSON.parse(str); // 从查询参数获取推荐的书籍数据
              var result=[]
              for(let i=0;i<this.searchedBooks.length;i++){
                // console.log(JSON.parse(this.recommendedBooks[i]))
                result.push(JSON.parse(this.searchedBooks[i]))
              }
              this.searchedBooks=result
           
        })
        .catch(error => {
          console.error(error);
          // 处理请求失败的情况
        });
    },
    methods: {
    goToBookDetails(bookId) {
      // 跳转到书籍详细页面
      this.$router.push(
        {
          name: 'book',
          // 设置路由参数
          params: {
              bookid: bookId
          }
        }
      );
    }
  }
  };
  </script>
  
  <style>
  .book-item {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .book-image img {
    width: 100px;
    height: auto;
  }
  
  .book-details {
    flex: 1;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  h3 {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  p {
    margin: 5px 0;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  </style>