<template>
    <header>
    <nav class="navbar">
        <div class="header">
        <h1 class="title">Book Recommend</h1>
        <!-- <SearchBook></SearchBook> -->
        <ul class="nav-list">
          
        <li v-for="item in menuItems" :key="item.id" class="nav-item">
          <router-link :to="getLink(item)">{{ item.title }}</router-link>
        </li>
      </ul>
        <!-- <SearchBook></SearchBook> -->
        <div class="search-container">
          <div class="search">
            <input type="text" v-model="searchInput" :clearable="true" placeholder="输入作品/作者/译者/标签" />
          </div>
          <div class="search-button">
            <button type="info" plain @click="submitSearch">搜索</button>
          </div>
        </div>
      
        <!-- </ul> -->
  </div>
    </nav>
    </header> 
  </template>
  
  <script>
  // import SearchBook from './SearchBook.vue';

  export default {
    name : 'AfterLoginHeader',
    // components:{
    //   SearchBook
    // },
    data() {
      return {
        menuItems: [
          { id: 1, title: '标签推荐', link: '/label-recommend/:userid'},
          { id: 2, title: '实时推荐', link: '/realtime-recommend/:userid'},
          // { id: 3, title: '我的评分', link: '#' }
        ],
        searchInput: ''
      };
    },
    computed: {
      userId() {
        // return store.state.userid;
        return localStorage.getItem('userid')
      }
    },
    methods: {
      getLink(menuItem) {
        if (menuItem.link.includes(':userid')) {
          return menuItem.link.replace(':userid', this.userId);
        }
        return menuItem.link;
      },
      submitSearch() {
      const searchQuery = this.searchInput;
      console.log(searchQuery)
      this.$router.push({
        name:'search',
        params:{
            query:searchQuery
        }
      });
    }
  }
};
  </script>
  
  <style scoped>
  .header {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 5%;
    padding-right: 2%;
    }
  .title{
    font-size: 50px;
    margin: 20px;
    font-family: STXingkai;
  }
  .navbar {
    /* position: fixed; */
    background-color: #efeded;
    padding: 1px;
    height: 50;
    margin-top: 0;
    left: 0;
    right: 0;
  }
  
  .nav-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    /* margin-right: 20%; */
  }
  
  .nav-item {
    display: inline-block;
    margin-right: 10px;
  }
  
  .nav-item a {
    text-decoration: none;
    color: #333;
    padding: 5px;
  }
  
  .nav-item a:hover {
    color: #7c8691;
  }
  .search-container {
  display: flex;
  align-items: center;
  max-width: 400px;
  margin: 1 auto;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 20px;
}

input[type="text"] {
  flex-grow: 1;
  border: none;
  outline: none;
  padding: 5px;
}

button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #659ad3;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

#search-results {
  max-width: 400px;
  margin: 20px auto;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

  </style>