import { createStore } from 'vuex';


const store= createStore({
  state: {
    userid: null
  },
  mutations: {
    setUserId(state, userid) {
      state.userid = userid;
    },
    logout(state) {
      state.userid = null;
    }
  },
  actions: {
    login({ commit }, userid) {
      commit('setUserId', userid);
    },
    logout({ commit }) {
      commit('logout');
    }
  },
  getters: {
    currentUser: state => state.userid
  }
});
export default store;