<template>
    <AfterLoginHeader></AfterLoginHeader>
    <div class="container">
      <!-- <nav class="navbar">
        <h2 class="navbar-title">Book Recommend</h2>
      </nav> -->
      <h2 class="title">用户登录</h2>
      <form @submit.prevent="login" class="register-form">
        <div class="form-group">
          <label for="userid">用户ID：</label>
          <input type="text" id="userid" v-model="userid" required>
        </div>
        <div class="form-group">
          <label for="password">密码：</label>
          <input type="password" id="password" v-model="password" required>
        </div>
        <div class="button-group">
          <button type="submit" class="register-button">登录</button>
          <p class="info">没有账号？<router-link to="/register">立即注册</router-link></p>
        </div>
      </form>
    </div>
  </template>

  <script>
  // import LoginHeader from "../components/LoginHeader.vue";
  import AfterLoginHeader from '@/components/Header.vue';
  import axios from 'axios';
  // import store from "@/store";
  
    export default {
        name:'LoginForm',
        components:{
            AfterLoginHeader
        },
        data() {
            return {
            userid: '',
            password: ''
            };
        },
        // computed: {
        //   userId() {
        //     // return store.state.userid;
        //     return localStorage.getItem('userid')
          
        //   }
        // },
        methods: {
            async login() {
            // 验证用户名和密码
            try {
                const response = await axios.post('/login', {
                userid: this.userid,
                password: this.password
                });
                if (response.data.respond=='ok')
                {
                  console.log('1')
                // 将 userId 存储到 Vuex Store
                this.$store.commit('setUserId', this.userid);

                localStorage.setItem('userid', this.userid);
                
                // 登录成功后
                this.$router.push({
                        name: 'labelrecommend',
                        // 设置路由参数
                        params: {
                            userid: this.userid
                        }
                      }
                      )
                // axios.get(`/api/recommendations/${userid}`).then(response => {
                //     const recommendedBooks = response.data;
                //     // 在这里处理推荐的书籍数据
                //     this.$router.push({
                //         name: 'labelrecommend',
                //         // 设置路由参数
                //         params: {
                //             userid: userid
                //         },
                //         query: {
                //             books: JSON.stringify(recommendedBooks)
                //         }
                //         });
                //     }).catch(error => {
                //     console.error(error);
                //     // 处理请求失败的情况
                //     });

                }
                } catch (error) {
                    console.error(error);
        // 处理登录失败的情况
                }
            }
        }
    };
  </script>
  
  <style scoped>
  .container {
    background-color: #f8f8f8;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    /* justify-content: center; */
    margin-left: 0;
    margin-right: 0;
  }
  
  .title {
    color: #2c3e50;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .register-form {
    margin: 10px;
    padding: 40px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 10px 16px rgba(0, 0, 0, 0.1);
  }
  
  .form-group {
    margin-bottom: 20px;
  }

  
  label {
    display: block;
    margin-bottom: 10px;
    color: #555;
    font-weight: bold;
  }
  
  input[type="text"],
  input[type="password"] {
    width: 250px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f5f5f5;
    color: #333;
  }
  
  button[type="submit"] {
    background-color: #3498db;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button[type="submit"]:hover {
    background-color: #2980b9;
  }
  
  .info {
    text-align: center;
    font-size: 14px;
    color: #777;
    margin-top: 10px;
  }
  
  .info a {
    color: #3498db;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .info a:hover {
    color: #2980b9;
  }
  </style>