<template>
    <AfterLoginHeader></AfterLoginHeader>
    <div>
      <h2>根据您的评价记录，猜您喜欢……</h2>
      <p>用户ID: {{ userId }}</p>
      <ul>
        <li v-for="book in recommendedBooks" :key="book.bookId" class="book-item">
          <div class="book-image">
            <img :src="book.imlUrl" :alt="book.name" />
          </div>
          <div class="book-details">
            <h3>{{ book.name }}</h3>
            <p v-if="book.authors.length">作者: {{ book.authors }}</p>
            <p v-if="book.translators.length">译者: {{ book.translators }}</p>
            <p>评分: {{ book.rating }}</p>
            <p>出版社: {{ book.publisher }}</p>
          </div>
        </li>
      </ul>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
//   import store from '@/store';
  import AfterLoginHeader from '@/components/Header.vue';

  export default {
    name:'RealTimeRecommend',
    components:{
        AfterLoginHeader
    },
    data() {
      return {
        userid: null,
        recommendedBooks: []
      };
    },
    computed: {
      userId() {
        // return store.state.userid;
        return localStorage.getItem('userid')
      
      }
    },
    created() {
      // 发送get请求
      axios.get('/home',{
        params: {
              userid: this.userId
            }
      }).then(response => {
                    const recommendedBook = response.data;
                    this.userid = this.userId; 
                    // this.recommendedBooks = JSON.parse(JSON.stringify(recommendedBooks)); // 从查询参数获取推荐的书籍数据
                    var str=JSON.stringify(recommendedBook)
                    // console.log(str)
                    this.recommendedBooks = JSON.parse(str); // 从查询参数获取推荐的书籍数据
                    var result=[]
                    for(let i=0;i<this.recommendedBooks.length;i++){
                        // console.log(JSON.parse(this.recommendedBooks[i]))
                        result.push(JSON.parse(this.recommendedBooks[i]))
                    }
                    this.recommendedBooks=result
                
                    console.log(this.recommendedBooks)
                    console.log(this.recommendedBooks.length)
                    }).catch(error => {
                    console.error(error);
                    // 处理请求失败的情况
                    });
    }
  };
  </script>
  
  <style>
  .book-item {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .book-image img {
    width: 100px;
    height: auto;
  }
  
  .book-details {
    flex: 1;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  h3 {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  p {
    margin: 5px 0;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  </style>